.training-route {
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    position: relative;
    margin-left: 10px;

    @include mobile-width {
      margin-left: 14px;
    }

    h3 {
      @include mobile-width {
        margin-left: 14px;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: auto;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: rgba(2, 102, 193, 0.2);
    }

    @include tablet-width {
      grid-template-columns: 1fr;
      gap: 53px;
      padding-top: 15px;

      &::before {
        visibility: hidden;
      }
    }

    @include mobile-width {
      padding-top: 26px;
      gap: 25px;

      &::before {
        visibility: visible;
        height: 79%;
        width: 1px;
        top: 36px;
        left: 7px;
      }
    }
  }

  &__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    position: relative;

    @include tablet-width {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 89%;

      &::before {
        content: "";
        position: absolute;
        top: auto;
        left: 0;
        bottom: 0;
        width: 60%;
        height: 1px;
        background-color: rgba($color-navy-blue, 0.2);
      }

      &:nth-child(2n) {
        flex-direction: row-reverse;

        &::before {
          top: 0;
          bottom: auto;
        }

        &::after {
          content: "";
          position: absolute;
          background-color: rgba($color-navy-blue, 0.2);
          width: 1px;
          height: 55px;
          top: auto;
          left: 59.2%;
          right: auto;
          bottom: 80px;
        }

        & .training-route__card::after {
          top: -6px;
          bottom: auto;
        }
      }
    }

    @include mobile-width {
      width: 100%;
      flex-direction: column;
      gap: 25px;

      &::before {
        display: none;
      }

      &:nth-child(2n) {
        flex-direction: column;

        &::after {
          display: none;
        }

        & .training-route__card::after {
          top: calc(50% - 6px);
          left: 0;
        }
      }
    }
  }

  &__card {
    display: grid;
    grid-template-columns: 25px 1fr;
    gap: 15px;
    max-width: 280px;
    min-height: 100%;
    padding-bottom: 34px;
    padding-top: 41px;
    position: relative;

    @include tablet-width {
      max-width: 256px;
      font-size: 14px;
      line-height: 22px;
      padding-bottom: 15px;
      padding-top: 20px;
    }

    @include mobile-width {
      grid-template-columns: 19px 1fr;
      max-width: 100%;
      padding: 0;
      padding-left: 33px;
      font-size: 12px;
      line-height: 18px;
      gap: 12px;

      &::after {
        top: calc(50% - 6px);
        left: 0;
        width: 13px;
        height: 13px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: auto;
      left: 0;
      bottom: -7px;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: #0266c1;

      @include mobile-width {
        bottom: 8px;
      }
    }


    svg {
      width: 25px;
      height: 25px;
      align-self: center;
      justify-self: center;

      @include mobile-width {
        width: 19px;
        height: 19px;
      }
    }

    p {
      padding: 0;
      margin: 0;
      line-height: 24px;
      align-self: flex-end;
      max-width: 100%;
      color: $color-elephant;


      @include mobile-width {
        line-height: 18px;
        font-size: 12px;
        font-weight: 400;
        max-width: 80%;
      }
    }
  }
}
